import React from 'react';
import QRCodeImg from "../qr-code/qr-code";
import {generate, copyToClipboard} from '../../utils';
import "./voucher-code.css";

export const VoucherCode = ({language}) => {
    const redemptionCode = generate(4, [4, 3, 3, 4]);

    const handleCopyBtnClick = (e) => {
        const msg = "Code has been copied to clipboard successfully";

        copyToClipboard(redemptionCode, msg);
    };

    return (
        <div className="voucher-code">
            {redemptionCode ? (
                <>
                    <div>
                        <p className="code" id="code-value">
                            {redemptionCode}
                        </p>

                        <div>
                            {language === "ar" ? "يمكنك" : "You can"}

                            <button
                                className="copy-code"
                                onClick={handleCopyBtnClick}
                            >
                                {language === "ar" ? "نسخ" : "Copy"}
                            </button>

                            {language === "ar" ? "القسيمة" : "Code"}
                        </div>

                        <p>{language === "ar" ? "أو قم بمسح صورة QR باستخدام جوالك" : "Or Scan the QR Code"}</p>
                    </div>
                    <div>
                        <QRCodeImg data={redemptionCode}/>
                    </div>
                </>
            ) : (
                <p className="voucher-code" id="code-value">
                    {language === "ar"
                        ? "الرابط غير صالح , قم بتحديث الصفحة رجاءً"
                        : "Invalid link, Please refresh the page"}
                </p>
            )}
        </div>
    );
};

export default VoucherCode;
