import React, {useEffect, useState} from "react";
import NavBar from "./components/nav-bar/nav-bar";
import Congratulations from "./components/congratulation/congratulation";
import VoucherCode from "./components/voucher-code/voucher-code";
import InfoCard from "./components/info-cards/info-card";
import {useLocalStorage} from "./hooks/use-local-storage";
import './App.css';

function App() {
    const [storedValue, setStoredValue] = useLocalStorage("app_lang", "ar");
    const [language, setLanguage] = useState(storedValue);
    const [renderContent, setRenderContent] = useState(false);

    useEffect(() => {
        setRenderContent(true);
        setLanguage(storedValue);
    }, [storedValue]);

    const handleLanguageChange = (language) => {
        setStoredValue(language);
    };

    const renderApp = () => {
        if (!renderContent) {
            return <div></div>;
        }

        return (
            <>
                <div className={language === 'ar' ? 'ar' : ''}>
                    <NavBar language={language} onLanguageChange={handleLanguageChange} />

                    <section className='influencer'>
                        <Congratulations language={language}/>
                        <VoucherCode language={language}/>
                    </section>

                    <InfoCard language={language}/>
                </div>
            </>
        );
    };

    return renderApp();
}

export default App;
