import React, {useState} from "react";
import "./nav-bar.css";

function NavBar({language, onLanguageChange}) {
    const [sideNavActive, setSideNavActive] = useState(false);

    const navLinksEn = [
        {title: "Home", link: "https://www.redeemly.com/"},
        {title: "About", link: "https://www.redeemly.com/about/"},
        {title: "Services", link: "https://www.redeemly.com/#service"},
        {title: "Products", link: "https://www.redeemly.com/"},
        {title: "Contact Us", link: "https://www.redeemly.com"},
    ];

    const navLinksAR = [
        {title: "الصفحة الرئيسية", link: "https://www.redeemly.com/"},
        {title: "عن الشركة", link: "https://www.redeemly.com/about/"},
        {title: "الخدمات", link: "https://www.redeemly.com/#service"},
        {title: "المنتجات", link: "https://www.redeemly.com/"},
        {title: "تواصل معنا", link: "https://www.redeemly.com"},
    ];

    const navLinks = language == "ar" ? navLinksAR : navLinksEn;

    const onLanguageBtnClick = () => {
        if (language === "ar") {
            onLanguageChange("en");
        } else {
            onLanguageChange("ar");
        }
    };

    const renderLinks = () => {
        return (
            <>
                {navLinks.map((link) => (
                    <a key={link.title} href={link.link} target="_blank">
                        {link.title}
                    </a>
                ))}

                <a onClick={onLanguageBtnClick}>{language === "ar" ? "تغيير اللغة" : "Change Language"}</a>
            </>
        );
    };

    return (
        <>
            <div
                className={`side-nav ${sideNavActive ? 'active' : ''}`}
                onClick={() => sideNavActive && setSideNavActive(!sideNavActive)}
            >
                <span
                    className="close-btn fas fa-times"
                    onClick={() => {
                        setSideNavActive(false);
                    }}
                />

                {renderLinks()}
            </div>

            <header className="header">
                <img
                    src="https://redeemly.fra1.digitaloceanspaces.com/redeemly-logo-b2.png"
                    alt="redeemly"
                />

                <nav className="desktop-nav">{renderLinks()}</nav>

                <nav className="mobile-nav">
                    <span
                        className="open-menu fas fa-bars"
                        onClick={() => {
                            setSideNavActive(true);
                        }}
                    />
                </nav>
            </header>
        </>
    );
}

export default NavBar;
